<template>
  <main>
    <p class="next_mission">NEXT MISSION</p>
    <div id="result">
      <div id="result_content" class="transition">
        <h2>{{ result.ID }}</h2>
        <h1>{{ result.name }}</h1>
        <div class="location">
          <img src="@/assets/statusbar.png" />
          <div class="location_type">
            <img :src="require(`@/assets/sta${result.status}.png`)" />
          </div>
        </div>
        <div class="attention">
          <img
            v-if="
              result.status == 1 ||
              result.status == 2 ||
              result.status == 3 ||
              result.status == 5
            "
            src="@/assets/attention.png"
          />
        </div>
      </div>
      <div id="loading" class="transition">
        <div class="loadmark">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <ul id="checkbox">
      <li>
        <input
          type="checkbox"
          id="checkbox-main"
          v-model="select_main"
          @change="targetselect"
        />
        EDF6 MAINSTORY
      </li>
      <li>
        <input
          type="checkbox"
          id="checkbox-dlc1"
          v-model="select_dlc1"
          @change="targetselect"
        />
        LOST DAYS
      </li>
      <li>
        <input
          type="checkbox"
          id="checkbox-dlc2"
          v-model="select_dlc2"
          @change="targetselect"
        />
        VISIONS OF MALICE
      </li>
    </ul>
    <p class="appname">EARTH DEFENCE FORCES 6 MISSION SELECTOR</p>
    <h4 id="btn_next" class="transition" @click="start">NEXT</h4>
  </main>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      select_main: true,
      select_dlc1: true,
      select_dlc2: true,
      target: Array,
      result: this.$missions.Main[0],
    };
  },
  mounted() {
    this.targetselect();
  },
  components: {},
  methods: {
    targetselect() {
      let list = [];
      if (this.select_main) {
        list = list.concat(this.$missions.Main);
        console.log("本編ミッションを含みます");
      }
      if (this.select_dlc1) {
        list = list.concat(this.$missions.DLC1);
        console.log("DLC1を含みます");
      }
      if (this.select_dlc2) {
        list = list.concat(this.$missions.DLC2);
        console.log("DLC2を含みます");
      }
      if (!this.select_main && !this.select_dlc1 && !this.select_dlc2) {
        list = null;
      }
      this.target = list;
      console.log("ターゲット変更");
      console.log(this.target);
    },
    start() {
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.add("nowloading");
      }
      console.log(elm);
      setTimeout(this.shuffle, 800);
    },
    shuffle() {
      if (this.target == null) {
        this.result = this.$missions.Main[0];
      } else {
        const rnd = Math.floor(Math.random() * this.target.length);
        this.result = this.target[rnd];
      }
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.remove("nowloading");
      }
    },
  },
};
</script>

